import React from "react";
import { createStyles, makeStyles, fade } from "@material-ui/core/styles";
import { connect } from "react-redux";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import SearchIcon from "@material-ui/icons/Search";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import Popover from "@material-ui/core/Popover";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import InputBase from "@material-ui/core/InputBase";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import CarIcon from "@material-ui/icons/DirectionsCar";
import SettingsIcon from "@material-ui/icons/Settings";
// import PaymentIcon from "@material-ui/icons/Payment";
import AccountBalanceIcon from "@material-ui/icons/AccountBalance";
import _debounce from "lodash/debounce";
import { useHistory } from "react-router-dom";

import Logo from "../../imgs/logoTransparent.png";
import { setSearching } from "../../store/vehicles/actions";
import { selectVehicleSearch } from "../../store/vehicles/selectors";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      // marginRight: theme.spacing(0.5),
    },
    logoImg: {
      width: 25,
      height: 25,
      marginRight: theme.spacing(1),
    },
    title: {
      flexGrow: 1,
    },
    location: {
      fontWeight: 500,
      display: "flex",
      alignItems: "center",
      marginRight: theme.spacing(2),
    },
    downArrow: {
      fontSize: "1rem",
    },
    popover: {
      pointerEvents: "none",
    },
    popoverTitleContainer: {
      display: "flex",
      alignItems: "center",
      marginBottom: theme.spacing(1),
    },
    popoverTitle: {
      margin: 0,
    },
    locationIcon: {
      fontSize: "1rem",
    },
    phoneNumber: {
      color: theme.palette.info.light,
      textDecoration: "underline",

      marginTop: theme.spacing(1),
    },
    paper: {
      padding: theme.spacing(1),
      width: 300,
    },
    search: {
      position: "relative",
      borderRadius: theme.shape.borderRadius,
      backgroundColor: fade(theme.palette.common.white, 0.15),
      "&:hover": {
        backgroundColor: fade(theme.palette.common.white, 0.25),
      },
      marginLeft: 0,
      width: "100%",
      [theme.breakpoints.up("sm")]: {
        marginLeft: theme.spacing(1),
        width: "auto",
      },

      "@media only screen and (max-width: 600px)": {
        display: "none",
      },
    },
    searchIcon: {
      padding: theme.spacing(0, 2),
      height: "100%",
      position: "absolute",
      pointerEvents: "none",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
    inputRoot: {
      color: "inherit",
    },
    inputInput: {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
      transition: theme.transitions.create("width"),
      width: "100%",
      [theme.breakpoints.up("sm")]: {
        width: "12ch",
        "&:focus": {
          width: "20ch",
        },
      },
    },
    drawer: {
      width: 250,
    },
    fullList: {
      width: "auto",
    },
  })
);

function ButtonAppBar({ searching, setSearching }) {
  const classes = useStyles();
  let history = useHistory();
  const [searchText, setSearchText] = React.useState("");
  const [drawerOpen, setDrawerOpen] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const pathname = window.location.pathname;

  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handlePopoverClose = () => {
    setAnchorEl(null);
  };

  const debounceHandler = React.useCallback(
    _debounce((value) => {
      setSearching(false);
    }, 1000),
    []
  );

  const handleChange = React.useCallback(
    (e) => {
      setSearchText(e.target.value);

      if (!searching) {
        setSearching(true);
      }

      debounceHandler(e.target.value);
    },
    [searching, setSearching, debounceHandler]
  );

  const handleDrawerChange = () => setDrawerOpen(!drawerOpen);

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
            onClick={handleDrawerChange}
          >
            <MenuIcon />
          </IconButton>
          <img
            src={Logo}
            alt="Mayfair Motors Logo"
            className={classes.logoImg}
          />
          <Typography variant="h6" className={classes.title}>
            Mayfair Motors
          </Typography>
          <div
            className={classes.location}
            onMouseEnter={handlePopoverOpen}
            onMouseLeave={handlePopoverClose}
          >
            <span>Roebuck, SC</span>
            <ArrowDropDownIcon className={classes.downArrow} />
            <Popover
              id="mouse-over-popover"
              className={classes.popover}
              classes={{
                paper: classes.paper,
              }}
              open={open}
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              onClose={handlePopoverClose}
              disableRestoreFocus
            >
              <div>
                <span className={classes.popoverTitleContainer}>
                  <h3 className={classes.popoverTitle}>Roebuck, SC</h3>{" "}
                  <LocationOnIcon className={classes.locationIcon} />
                </span>
                <div>161 Wingo Rd</div>
                <div>Roebuck, SC 29376</div>
                <a className={classes.phoneNumber} href="tel:+1-864-804-7528">
                  864-804-7528
                </a>
              </div>
            </Popover>
          </div>

          {pathname === "/" && (
            <div className={classes.search}>
              <div className={classes.searchIcon}>
                <SearchIcon />
              </div>
              <InputBase
                placeholder="Search…"
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
                inputProps={{ "aria-label": "search" }}
                onChange={handleChange}
                value={searchText}
              />
            </div>
          )}

          <SwipeableDrawer
            anchor="left"
            open={drawerOpen}
            onClose={handleDrawerChange}
            onOpen={handleDrawerChange}
          >
            <Divider />
            <List className={classes.drawer}>
              <ListItem
                button
                key={"Vehicles"}
                onClick={() => {
                  history.push("/");
                  handleDrawerChange();
                }}
              >
                <ListItemIcon>
                  <CarIcon />
                </ListItemIcon>
                <ListItemText primary={"Vehicles"} />
              </ListItem>

              <ListItem
                button
                component="a"
                key="creditApp"
                onClick={() => {
                  handleDrawerChange();
                }}
              >
                <ListItemIcon>
                  <AccountBalanceIcon />
                </ListItemIcon>
                <ListItemText primary={"Credit Application"} />
              </ListItem>
              <ListItem
                button
                key={"Info"}
                onClick={() => {
                  history.push("/info");
                  handleDrawerChange();
                }}
              >
                <ListItemIcon>
                  <SettingsIcon />
                </ListItemIcon>
                <ListItemText primary={"Info"} />
              </ListItem>
            </List>
          </SwipeableDrawer>
        </Toolbar>
      </AppBar>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    searching: selectVehicleSearch(state),
  };
};

const AppWrapper = connect(mapStateToProps, {
  setSearching,
})(ButtonAppBar);

export default AppWrapper;
