import React from "react";
import { createStyles, makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";
import InfoIcon from "@material-ui/icons/Info";
import ToolTip from "@material-ui/core/Tooltip";
import Paper from "@material-ui/core/Paper";
import { IKImage } from "imagekitio-react";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      // margin: 16,
      border: "1px solid grey",
      borderRadius: 5,
      boxSizing: "border-box",
    },
    vehiclePic: {
      width: "100%",
      height: "auto",
      borderTopRightRadius: 5,
      borderTopLeftRadius: 5,
      // "&:hover": {
      //   cursor: "pointer",
      // },
    },
    info: {
      width: "100%",
      borderBottomRightRadius: 5,
      borderBottomLeftRadius: 5,
      // "&:hover": {
      //   cursor: "pointer",
      // },
      padding: 8,
      boxSizing: "border-box",
    },
    imgContainer: {
      width: "100%",
      //   height: 126,
      borderRadius: 5,
      // "& hover": {
      //   cursor: "pointer",
      // },
    },
    price: {
      marginTop: 4,
      fontSize: "1.3rem",
      color: "#575f65",
      fontWeight: 500,
      marginBottom: theme.spacing(1),
    },
    blueText: {
      color: "#1773ce",
      fontSize: "1rem",
    },
    payments: {
      marginTop: theme.spacing(1),
      fontSize: ".8rem",
    },
    paymentsSpan: {
      // "&:hover": {
      //   textDecoration: "underline",
      // },
      position: "relative",
    },
    infoIcon: {
      fontSize: ".7rem",
      position: "absolute",
      right: -theme.spacing(2),
      top: "50%",
      transform: "translateY(-50%)",
    },
    sold: {
      color: "red",
      fontWeight: "bold",
      marginLeft: theme.spacing(4),
    },
  })
);

const urlEndpoint = "https://ik.imagekit.io/o2fek1q3cpj/";

export default function ListItem({ vehicle }) {
  const classes = useStyles();
  const price = Number.parseFloat(vehicle.price.replace(/,/g, ""), 10);
  const payments = Math.floor(price / 60 + 40);

  return (
    <Grid
      item
      xs={12}
      sm={6}
      md={3}
      style={{ padding: 8 }}
      // onClick={() => history.push("/vehicle")}
    >
      <div className={classes.root}>
        <div className={classes.imgContainer}>
          <IKImage
            urlEndpoint={urlEndpoint}
            path={vehicle.img}
            transformation={[{ height: 300, width: 400 }]}
            lqip={{ active: true, quality: 20 }}
            loading="lazy"
            alt="Vehicle Pic"
            className={classes.vehiclePic}
            width="800"
            height="600"
          />
        </div>

        <Paper elevation={1} className={classes.info}>
          <div className={classes.blueText}>
            {`${vehicle.year} ${vehicle.make}`} {`${vehicle.model}`}
          </div>
          {/* <div className={classes.blueText}> </div> */}
          <div className={classes.price}>
            <span>{`$${vehicle.price}`}</span> ·{" "}
            <span>{`${vehicle.miles} miles`}</span>
          </div>

          <Divider />
          <div
            className={classes.payments}
            // onClick={(e) => {
            //   e.stopPropagation();
            //   history.push("/payments-calculator");
            // }}
          >
            <ToolTip position="bottom" title="Credit Application">
              <span className={classes.paymentsSpan}>
                Est. ${payments}/month
                <InfoIcon className={classes.infoIcon} />
              </span>
            </ToolTip>

            {/* <span> Call 864-804-7528</span> */}
            {vehicle.sold && <span className={classes.sold}>SOLD</span>}
          </div>
        </Paper>
      </div>
    </Grid>
  );
}
